* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  background-color: #f4f4f4;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  display: flex;
}

header {
  color: #fff;
  text-align: center;
  background-color: #ff4c4c;
  border-radius: 8px;
  width: 100%;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

h1 {
  font-size: 2.5rem;
}

main {
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 800px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

section {
  margin-bottom: 20px;
}

h2 {
  color: #333;
  margin-bottom: 10px;
  font-size: 1.5rem;
}

input[type="number"], input[type="text"] {
  box-sizing: border-box;
  border: 2px solid #ddd;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 10px;
  padding: 12px;
  font-size: 1rem;
  transition: border-color .3s;
}

input[type="number"]:focus, input[type="text"]:focus {
  border-color: #ff4c4c;
  outline: none;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #ff4c4c;
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  transition: background-color .3s;
}

button:hover {
  background-color: #e04343;
}

button:active {
  background-color: #cc3a3a;
}

ul {
  list-style-type: none;
}

ul li {
  background-color: #f9f9f9;
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

ul li:last-child {
  margin-bottom: 0;
}

section#summary p {
  font-size: 1.2rem;
  font-weight: bold;
}

section#summary p span {
  color: #333;
  font-size: 1.5rem;
}

section#summary p {
  margin-bottom: 8px;
}

@media (width <= 768px) {
  body {
    padding: 10px;
  }

  main {
    padding: 15px;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.3rem;
  }

  button, input[type="number"], input[type="text"] {
